
.btn {
	cursor: pointer;
	border-radius: 5px;
	margin-bottom: 15px;
    text-decoration: none;
	&:focus, &:active {
		outline: none;
		box-shadow: none;
	}

	&-default {
		background: #dddddd;
		color: #ffffff;
		&:hover {
			background: #9f9f9f;
			color: #ffffff;
		}
	}

	&-icon {
		padding-right: 43px;
		.btn-icon__content {
			position: relative;
			padding-left: 43px;
		}

		&__img {
			position: absolute;
			top: 50%;
			left: 0;
			transform: translateY(-50%);
			color: #fff;
			font-size: 24px;
			line-height: 18px;
		}
	}
}

.button {
    cursor: pointer;
    &-block {
        display: block;
        width: 100%;
    }
    &.button-black.alt {
        background-image: -webkit-gradient(linear,left top,left bottom,from(transparent),color-stop(49%,transparent),color-stop(50%,#000),to(#000));
        background-image: linear-gradient(180deg,transparent 0,transparent 49%,#000 50%,#000 100%);
        color: #000;
        border-color: #000000;
        &:hover,&:active, &:focus {
            color: #ffffff;
        }
    }
}
