@media (min-width: 992px) {
    .search-filter {
        &__row {
            margin: 0 -7.5px;
        }
        &__col {
            float: left;
            width: 20%;
            padding: 0 7.5px;
        }
    }
    .footer__center {
        .row {
            flex-direction: row-reverse;
        }
    }
    .video-back {
        background-image: none!important;
    }
    .hidden-desktop {
        display: none;
    }
}
@media (min-width: 768px) {
    .mobile-open {
        display: none;
    }
    .header {
        &__logo {
            padding-top: 4px;
            margin-bottom: 4px;
        }
        &__right {
            margin-left: 140px;
        }
        &__bottom {
            position: relative;
            z-index: 2;
        }
        &__top {
            a {
                i {
                    display: none;
                }
            }
        }
    }
    .menu-toggle {
        display: none;
    }

    .top-menu {
        display: block!important;
        >ul {
            display: flex;
            ul {
                position: absolute;
                left: 0;
                top: 100%;
                padding-top: 1px;
                li {
                    margin-bottom: 1px;
                    background: $base-color;
                    a {
                        display: block;
                        padding: 15px 0 13px 20px;
                    }
                    &:hover {
                        background-color: $base-color-hover;
                    }
                }
                ul {
                    left: 100%;
                    top: 0;
                    margin-top: 0;
                    margin-left: 1px;
                    padding-top: 0;
                }
            }
            >li {
                margin-top: 1px;
                padding: 0 20px 15px;
            }
            li {
                &:hover {
                    >ul {
                        display: block;
                    }
                }
            }
        }
    }

    .filter-mobile,.mixed-filters .filter-bar__button {
        display: none;
    }
    .mixed-filters {
        display: block !important;
    }
    .js-toggle-mobile-filter {
        display: none;
    }
}

@media (min-width: 481px) {
    .visible-mobile {
        display: none;
    }
}


$icons-top: 25px;
@media (max-width: 1023px) {
    .fb-nav {
        ul {
            text-align: center;
             li {
                 float: none;
                 display: inline-block;
             }
        }
    }
    .copyright {
        text-align: center;
    }
}

@media (max-width: 991px) {
    .video-back {
        background-position: center center;
        background-repeat: no-repeat;
        video {
            display: none;
        }
    }
    .container {
        max-width: 100%;
    }
    .top-menu>ul>li {
        &:last-child, &:nth-last-child(2), &:nth-last-child(3) {
            ul ul  {
                left: auto;
                right: 100%;
            }
        }
    }
    .footer__subscribe {
        margin-bottom: 25px;
    }
    .footer__social-items {
        text-align: center;
    }
}
@media (max-width: 767px) {
    h1,.h1 {
        font-size: 22px;
    }
    h2,.h2 {
        font-size: 18px;
    }
    .header {
        padding: 5px 0;
        position: relative;
        &__search-label {
            display: none;
        }
        &__menu {
            clear: both;
            margin: 0 -15px;
            .top-menu {
                min-width: 340px;
                background: $base-color;
                position: absolute;
                top: 100%;
                right: 0;
            }
        }
        &__top {
            position: absolute;
            right: 88px;
            top: $icons-top;
            .hsep {
                display: none;
            }
            a {
                display: inline-block;
                margin: 0 10px;
                i {
                    display: block;
                    font-size: 22px;
                }
                span {
                    display: none;
                }
            }
        }
        .js-search-trigger {
            position: absolute;
            right: 55px;
            top: $icons-top;
            svg {
                width: 22px;
                height: 22px;
            }

        }
    }
    .top-menu {
        display: none;
        ul {
            li {
                a {
                    padding: 16px 16px;
                }
                ul {
                    li {
                        padding-left: 30px;
                        a {
                            border-color: transparent!important;
                        }
                    }
                }
            }
        }
        >ul {
            flex-flow: column wrap;
            justify-content: space-around;
            padding: 0;
            text-align: left;
            li:first-child {
                a {
                    border-top: 1px solid #fff;
                }
            }
            a {
                display: block;
                padding: 15px;
                border-bottom: 1px solid #fff;
                text-align: left;
                &:after {
                    display: none;
                }
            }
            li {
                padding: 0;
                margin-bottom: 0;
                ul {
                    li {
                        background-color: $base-color;
                        a {
                            border:0;
                        }
                    }
                }
            }
        }
    }
    .mobile-open {
        width: 49px;
        height: 49px;
        position: absolute;
        right: 0;
        top: 0;
        &:before,&:after {
            content: '';
            display: block;
            width: 2px;
            background: #ffffff;
            height: 20px;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%,-50%);
            transition: height 200ms ease;
        }
        &:after {
            transform: translate(-50%,-50%) rotate(90deg);
        }
        &.is-open {
           &:before {
               height: 0;
           }
        }
    }
    .fb-nav ul li {
        display: block;
    }

    .footer__subscribe {
        margin-bottom: 40px;
        .form-group {
            max-width: 100%;
            padding-right: 93px;
            input {
                max-width: 100%;
            }
        }
        .form-group .btn {
            font-size: 12px;
            padding: 7px;
        }
    }
    .homepage-email__form-fields {
        max-width: 100%;
        min-width: auto;
    }

    .top-block {
        padding-top: 10px;
        h1 {
            font-size: 28px;
        }
    }

    .mixed-filters {
        display: none;
        &.active {
            display: block;
        }
    }

    .content-menu {
        ul {
            text-align: center;
            li {
                display: inline-block;
                float: none;
            }
        }
    }

    .attribute-item {
        margin-bottom: 20px;
    }
   
}

@media (max-width: 480px) {
    .site-logo {
        width: 80px;
        height: 40px;
    }
    .header {
        &__top, .js-search-trigger {
            top: 8px!important;
        }
        &__menu {
            .top-menu {
                min-width: 100%;
                left: 0;
            }
        }
    }
    .menu-toggle {
        top: 6px;
    }
    .top-block {
        h1 {
            text-align: center;
            clear: both;
        }
    }
    .quests {
        h2 {
            font-size: 24px;
            text-align: center;
        }
    }
    .mixed-filters {
        .checkbox {
            display: block;
        }
    }
    .visible-desktop {
        display: none;
    }
}



