/*Header*/

$toggleColor: #fff;
$menusize: 10px;

.header {
    background: $base-color;
    column-rule: #ffffff;
    position: relative;
    z-index: 2;
    .container {
        position: relative;
    }
    a {
        color: #ffffff;
        font-weight: 700;
        &:hover {
            color: #ffffff;
        }
    }
    &__logo {
        float: left;
        a:hover {
            text-decoration: none;
        }
    }
    &__top {
        text-align: right;
        font-size: 14px;
        margin-bottom: 18px;
        a {
            color: #ffffff;
            display: inline-block;
            vertical-align: middle;
            text-decoration: none;
            span {
                border-bottom: 1px solid transparent;
            }
            line-height: 14px;
            &:hover {
                color: #ffffff;
                text-decoration: none;
                span {
                    border-bottom-color: #fff;
                }
            }
        }
    }
}

.hsep {
    display: inline-block;
    vertical-align: middle;
    margin: 0 9px;
    width: 1px;
    height: 10px;
    background-color: #ffffff;
}

.site-logo {
    width: 127px;
    height: 64px;
    position: relative;
    a {
        position: absolute;
        left: 0;
        bottom: 0;
        right: 0;
        top: 0;
        display: block;

        &:hover {

        }
    }
}

.top-menu {
    >ul {
        list-style: none;
        margin: 0;
        display: flex;
        flex-flow: row wrap;
        justify-content: flex-start;
        ul {
            min-width: 250px;
            list-style: none;
            display: none;
        }
        li {
            position: relative;
            margin-bottom: 0;
            &:last-child {
                margin-bottom: 0;
            }
        }
        >li {
            &.active, &.has-active, &:hover{
                > a {
                    &:after {
                        opacity: 1;
                    }
                }
            }
        }
        a {
            font-size: 14px;
            text-decoration: none;
            font-weight: 700;
            position: relative;
            color: #ffffff;
            &:hover {
                color: #ffffff;
            }
            &:after {
                content: '';
                position: absolute;
                bottom: -5px;
                left: 0;
                right: 0;
                display: block;
                height: 2px;
                background: #fff;
                transition: all 200ms ease;
                opacity: 0;
            }
        }

    }

}

.menu-toggle {
    font-size: 16px;
    cursor: pointer;
    right: 13px;
    top: 23px;
    position: absolute;
    z-index: 4;
    width: 1.4em;
    height: 1.625em;
    span {
        display: block;
        font-size: $menusize;
        position: absolute;
        top: 1.3em;
        width: 2em;
        height: 0.200em;
        background: $toggleColor;
        transition: background 0s 0.3s;
        &:before,
        &:after {
            content: "";
            position: absolute;
            display: block;
            left: 0;
            width: 100%;
            height: 0.200em;

            background: $toggleColor;
            transition-duration: 0.3s, 0.3s;
            -webkit-transition-duration: 0.3s, 0.3s;
            transition-delay: 0.3s, 0s;
            -webkit-transition-delay: 0.3s, 0s;
        }
        &:before {
            top: -1em;
            transition-property: top, transform;
            -webkit-transition-property: top, transform;
        }

        &:after {
            bottom: -1em;
            transition-property: bottom, transform;
            -webkit-transition-property: bottom, transform;
        }


    }
    &.active {
        span {
            background: none;
            &:before {
                top: 0;
                transform: rotate(45deg);
                -webkit-transform: rotate(45deg);
            }
            &:after {
                bottom: 0;
                transform: rotate(-45deg);
                -webkit-transform: rotate(-45deg);
            }
            &:before,
            &:after {
                transition-delay: 0s, 0.3s;
                -webkit-transition-delay: 0s, 0.3s;
            }
        }
    }
}

.search-block {
    position: absolute;
    margin-top: 2px;
    top: 100%;
    left: 0;
    right: 0;
    padding: 8px;
    background: $base-color;
    z-index: 1;
    display: none;
    &.is-open {
        display: block;
    }
    &__inner {
        position: relative;
        padding-right: 10%;
    }
    .form-group {
        margin-bottom: 0;
    }
    .form-control {
        @include placeholder {
            color: #dedfdf;
        };
        border-radius: 0;
        font-size: 40px;
        height: 58px;
        font-weight: 700;
        letter-spacing: 0;
        padding: 3px 20px 3px;
    }
    .button {
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        width: auto;
        min-width: 10%;
        text-align: center;
        cursor: pointer;
        svg {
            width: 40px;
            height: 40px;
        }
    }
}


/* /Header*/

