.juster {
  display: inline-block;
  width: 100%;
  height: 0;
  overflow: hidden;
}

.jfix-after:after {
  content: '';
  display: inline-block;
  width: 100%;
  height: 0;
  overflow: hidden;
}

.vfix {
  height: 100%;
  width: 0;
  display: inline-block;
  vertical-align: middle;
}

.vfix-before:before {
  content: '';
  height: 100%;
  width: 0;
  display: inline-block;
  vertical-align: middle;
}

.hidden-block {
  position: absolute;
  left: -99999em;
  top: -99999em;
}

/* Main */
.home-text {
  text-align: center;
  margin-bottom: 50px;

  h1,.h1 {
    margin: 0;
  }
}

.search-filter {
  margin-bottom: 30px;

  .row {
    margin: 0 -3px;

    [class*="col-"] {
