    margin-bottom: 15px;
  }
}

.video-back {
  position: absolute !important;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
}

.slick-carousel {
  opacity: 0;
  transition: opacity 200ms ease;
  margin-bottom: 40px;
}

.carousel-item {
  display: block;
  color: #000000;
  padding: 0 12px;
  max-width: 280px;
  float: left;

  &__img {
    margin-bottom: 20px;

    &:before {
      content: '';
      display: block;
      width: 100%;
      height: 0;
      padding-top: 120%;
    }

    .btn {
      align-self: center;
      background-color: $base-color;
      color: #ffffff;
      min-width: 122px;
      font-size: 14px;
      font-weight: 600;
      opacity: 0;
      padding: 7px 20px;
      position: absolute;
      z-index: 2;
      border-radius: 0;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }

    position: relative;
    overflow: hidden;

    img {
      z-index: 1;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      transition: all 200ms ease;
      max-height: 100%;
    }
  }

  &:hover {
    img {
      transform: translate(-50%, -50%) scale(1.05);
    }

    .btn {
      opacity: 1;
    }
  }

  &__text {
    padding-right: 25px;

    h3,.h3 {
      font-size: 16px;
      font-weight: 600;
      letter-spacing: 0.01em;
      line-height: 1.25;
      margin: 0;
    }

    p {

    }
  }

  &.carousel-item__absolute {
    padding: 15px 10px;
    position: relative;

    .carousel-item__img {
      z-index: 1;
      overflow: visible;

      &:before {
        padding-top: 147%;
      }
    }

    .carousel-item__text {
      padding-right: 0;
      z-index: 2;
      position: absolute;
      left: 25px;
      right: 25px;
      bottom: 35px;

      h3, .h3 {
        font-size: 19px;
        font-weight: 600;
        color: #ffffff;
        margin-bottom: 10px;
      }

      p {
        color: #ffffff;
        font-weight: 400;
      }
    }
  }
}

.slick-initialized {
  &.slick-carousel {
    opacity: 1;
  }

  .carousel-item {
    float: none;
    max-width: 100%;
  }
}

.quests {
  margin-bottom: 25px;

  .row {
