}

.quest-item {

  margin-bottom: 15px;
  display: block;
  color: #000000;

  .button {
    text-align: center;
  }

  .quest-item__price {
    position: absolute;
    right: 0;
    top: 60px;
    z-index: 2;

    .label {

      >span {
        position: relative;
        box-shadow: none;
        padding: 5px 10px;
        background: $base-color-hover;
