  }

  .label {
    display: block;
    margin-bottom: 8px;
    span {
      padding-left: 22px;
      position: relative;
      img {
        position: absolute;
        left: 0;
        top: 50%;
        height: 25px;
        margin-top: -12px;
      }
    }
  }



  &__img {
    position: relative;
    overflow: hidden;
    display: block;

    img {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      transition: all 200ms ease;
      z-index: 1;
      max-height: 100%;
      width: 100%;
    }

    &:before {
      content: '';
      display: block;
      width: 100%;
      height: 0;
      padding-top: 100%;
