  .queasts__col {
    @extend .col-lg-6;
    @extend .col-xl-4;
    padding: 0 7.5px;
  }

  .quest-item {
    position: relative;

    &__absolute {
      padding: 5px;
      //opacity: 0;
      //visibility: hidden;
