      right: 10px;
      bottom: 55px;
      z-index: 2;
      color: #fff;
    }

    &__title {
      text-transform: uppercase;
      font-size: 22px;
      margin-bottom: 5px;
    }
    &__subtitle {
      margin-bottom: 5px;
      font-size: 16px;
      text-transform: uppercase;
