    }

    &__address {
      padding-top: 8px;
    }

    &__description {
      height: 0;
      min-height: 0;
      overflow: hidden;
      margin-bottom: 0;
      max-height: 90px;
    }

    &:hover {
      .quest-item__description {
        height: auto;
      }
    }

    .label--type {
      position: absolute;
      top: 6px;
      left: 15px;
      color: #fff;
      font-size: 25px;
      font-weight: 700;
      z-index: 2;
      span {
        padding-left: 0;
      }
