    transition: all .2s linear;
  }
}

.quests-list {
  .queasts__col {
    margin-bottom: 0;
  }

  .img-white {
    display: none;
  }

  .quest-item {
    @extend .cfix;
    position: relative;
    border-top: 1px solid #dedede;
    border-right: 1px solid #dedede;
    border-left: 1px solid #dedede;
    margin-bottom: 0;


    .quest-item__price {
      right: auto;
      left: 0;
      top: 15px;
    }

    &__absolute {
      display: flex;
      flex-direction: column;
      .quest-item__title {
        order: 1;
      }
      .quest-item__subtitle {
        order: 2;
        margin-bottom: 10px;
      }
      .quest-item__address {
        order: 3;
        margin-bottom: 15px;
      }
      .quest-item__description {
        order: 4;
      }
    }

    &__image {
      position: relative;
      margin-bottom: 0;
      @media (min-width: 768px) {
        width: 300px;
        float: left;
      }
      @media (min-width: 480px) and (max-width: 767px) {
        width: 200px;
        float: left;
      }
    }

    &__title {
      font-size: 32px;
      margin-bottom: 10px;
      text-transform: uppercase;
      @media (max-width: 767px) {
        font-size: 18px;
      }
    }

    &__subtitle {
      text-transform: uppercase;
      font-size: 20px;
      margin-bottom: 20px;
      @media (max-width: 767px) {
        font-size: 18px;
      }
    }

    &__description {
      margin-bottom: 20px;
      font-size: 18px;
      font-weight: 300;
    }

    &__text {
      margin-left: 300px;
      @media (min-width: 481px) and (max-width: 767px) {
        margin-left: 200px;
      }
      @media (max-width: 480px) {
        margin-left: 0;
      }

      @media (min-width: 768px) {
        padding: 20px 18px;
      }
      @media (max-width: 767px) {
        padding: 10px;
      }
    }

    &__actions {
      padding-top: 15px;
    }

    &__labels {
      @media (min-width: 992px) {
        position: absolute;
        left: 320px;
        bottom: 0;
        display: flex;
        .label--type {
          width: 33.333%;
        }
        .bottom-labels {
          display: flex;
          justify-content: space-between;
          width: 64%;
        }
      }
      
      @media (max-width: 991px) {
        padding-top: 25px;
        position: relative;
        display: block;
        .bottom-labels {
          display: block;
        }
      }

      font-weight: 700;

      p {
        font-weight: 400;
        display: inline-block;
        margin-right: 10px;
        b {
          font-weight: 700;
        }
      }
    }

    &__labels {
      position: absolute;
      left: 0;
      right: 0;
    }

  }
}

