/*fonts*/

@mixin font-face($style-name, $file:$style-name, $weight:normal, $style:normal) {
	$filepath: "/fonts/" + $file;
	@font-face {
		font-family: quote($style-name);
		src: url($filepath + ".eot");
		src: url($filepath + ".eot?#iefix") format('embedded-opentype'),
		url($filepath + ".woff") format('woff'),
		url($filepath + ".ttf")  format('truetype'),
		url($filepath + ".svg#" + $style-name + "") format('svg');
		font-weight: $weight;
		font-style: $style;
	}
}


//  example->    @include font-face('PFDinTextPro', 'PFDinTextPro-Bold', 700, italic);

@font-face {
	font-family: 'icomoon';
	src:
					url('./fonts/icomoon.ttf?hpv0jm') format('truetype'),
					url('./fonts/icomoon.woff?hpv0jm') format('woff'),
					url('./fonts/icomoon.svg?hpv0jm#icomoon') format('svg');
	font-weight: normal;
	font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
	/* use !important to prevent issues with browser extensions that change fonts */
	font-family: 'icomoon' !important;
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;

	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}