
.footer {
    background-color: #fbfbfb;
    &_placeholder {
        height: 500px;
    }
    padding-top: 60px;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    color: #000006;
    a {
        color: #000006;
    }
    &__center {
        border-bottom: 1px solid #959798;
        border-top: 1px solid #959798;
        padding: 36px 0 28px;
        text-align: center;
    }
    &__bottom {
        padding: 28px 0 20px;
    }
    &__newsletter-submit.btn {
        border-radius: 0;
        background-color: #000006;
        color: #fff;
        font-size: 14px;
        line-height: 14px;
        font-weight: 600;
        min-width: 90px;
        height: 40px;
        padding: 7px 20px;
        margin: 0;
    }
    &__subscribe {
        .form-group {
            display: inline-block;
            position: relative;
            padding-right: 133px;
            .btn {
                position: absolute;
                right: 0;
                top: 0;
            }
            .error {
                display: none;
                position: absolute;
                top: 100%;
                left: 0;
                color: #a41e37;
                font-size: 12px;
            }
        }
        .is-invalid {
            &+.error {
                display: block;
            }
        }
    }
}

.copyright {
    padding-top: 3px;
    font-size: 12px;
    color: #65696a;
}

.f-nav {
    ul {
        list-style: none;
        margin: 0;
        li {
            margin-bottom: 7px;
            a {
                font-size: 16px;
                transition: all 200ms ease;
                text-decoration: none;
                color: #000006;
                font-weight: 700;
                &:hover {
                    color: #ee322d;
                }
            }
        }
    }
}

.f-address {
    margin-bottom: 40px;
    >a, >span {
        font-size: 14px;
        color: #000006;
        &+p {
            padding-top: 20px;
        }
    }
    p {
        font-size: 13px;
        margin-bottom: 10px;
        i {
            position: relative;
            top: -2px;
        }
    }
}

.footer__social-items {
    text-align: left;
}


.footer__social-item {
    display: inline-block;
    font-size: 0;
    margin: 0 0 0 20px;
    padding: 0;

    &:first-child {
        margin-left: 0;
    }

    .icon--fbook {
        width: 16px;
        height: 16px;
    }

    .icon--vk {
        width: 16px;
        height: 16px;
    }

    .icon--twitter {
        width: 24px;
        height: 16px;
    }


    .icon--pinterest {
        width: 24px;
        height: 16px;
    }

    .icon--instagram {
        width: 24px;
        height: 16px;
    }

    .icon--youtube {
        width: 42px;
        height: 16px;
    }
    &:hover {
        opacity: 0.7;
    }
}

.fb-nav {
    ul {
        @extend .cfix;
        list-style: none;
        margin: 0;
        li {
            margin-right: 20px;
            a {
                font-weight: 700;
                font-size: 12px;
                &:hover {
                    color: #ee322d;
                    text-decoration: underline;
                }
            }
            @media (min-width:768px) {
                float: left;
            }
        }
    }
}
