$line-color: #000;
$base-color: #2f0263;
$base-color-hover: #4f05a4;


// Px to em convert
@function em($pixels, $context: $browser-context) {
  @return #{$pixels/$context}em;
}

// Placeholer mixin
@mixin placeholder {
  &::-webkit-input-placeholder {
    @content
  }
  &:-moz-placeholder {
    @content
  }
  &::-moz-placeholder {
    @content
  }
  &:-ms-input-placeholder {
    @content
  }
}

html {
  height: 100%;
}

html, body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  min-width: 320px;
  min-height: 100%;
  position: relative;
  font-family: 'Open Sans', sans-serif;
  color: #000006;
  font-size: 16px;
  font-weight: 300;
  letter-spacing: 0.01em;
  line-height: 1.3;
  -webkit-font-smoothing: antialiased;
}

img {
  max-width: 100%;
  vertical-align: top;
}

* {
  outline: 0;
}

.text-right {
  text-align: right;
}

.container {
  max-width: 100%
}

.outerContainer {
  margin: 0 -15px;
}

.icon {
  vertical-align: middle;
}

.transform-center {
  transform: translate(-50%, -50%);
}

.inlineIcon-xs {
  display: none;
}

.cfix {
  &:after {
    content: '';
    display: block;
    height: 0;
    width: 100%;
    clear: both;
  }
}

.gray-bg {
  background: #f3f3f3;
  margin-bottom: 20px;
}

.pad-block {
  padding: 10px 10px;
}

.pull-right {
  float: right;
}

