/* ---- flickity-button ---- */
.flickity-button {
  background: rgba(255, 255, 255, 0.75);
  border: none;
  color: #333;
  position: absolute;
}

.flickity-button:hover {
  background: white;
  cursor: pointer;
}

.flickity-button:focus {
  -webkit-box-shadow: 0 0 0 5px #19F;
  box-shadow: 0 0 0 5px #19F;
  outline: none;
}

.flickity-button:active {
  opacity: 0.6;
}

.flickity-button:disabled {
  cursor: auto;
  opacity: 0.3;
  /* prevent disabled button from capturing pointer up event. #716 */
  pointer-events: none;
}

.flickity-button-icon {
  fill: #333;
}

/* ---- previous/next buttons ---- */
.flickity-prev-next-button {
  top: 50%;
  width: 44px;
  height: 44px;
  border-radius: 50%;
  /* vertically center */
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}

.flickity-prev-next-button.previous {
  left: 10px;
}

.flickity-prev-next-button.next {
  right: 10px;
}

/* right to left */
.flickity-rtl .flickity-prev-next-button.previous {
  left: auto;
  right: 10px;
}

.flickity-rtl .flickity-prev-next-button.next {
  right: auto;
  left: 10px;
}

.flickity-prev-next-button .flickity-button-icon {
  position: absolute;
  left: 20%;
  top: 20%;
  width: 60%;
  height: 60%;
}

/* ---- page dots ---- */
.flickity-page-dots {
  position: absolute;
  width: 100%;
  bottom: -25px;
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: center;
  line-height: 1;
}

.flickity-rtl .flickity-page-dots {
  direction: rtl;
}

.flickity-page-dots .dot {
  display: inline-block;
  width: 10px;
  height: 10px;
  margin: 0 8px;
  background: #333;
  border-radius: 50%;
  opacity: 0.25;
  cursor: pointer;
}

.flickity-page-dots .dot.is-selected {
  opacity: 1;
}

.tag {
  font-size: 13px;
  font-weight: 500;
  letter-spacing: 0.1em;
  line-height: 16px;
  text-transform: uppercase;
}

.link {
  border-bottom: 1px solid #000006;
  position: relative;
  display: inline-block;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 0.04em;
  line-height: 1.3;
  padding-bottom: 3px;
  text-decoration: none;
  text-transform: uppercase;
}

.link:after {
  border-bottom: 1.5px solid transparent;
  bottom: -1px;
  content: "";
  left: 0;
  position: absolute;
  right: 100%;
  -webkit-transition: right 0.8s 0.1s;
  transition: right 0.8s 0.1s;
}

@media screen and (hover: hover) {
  .link:hover {
    border-color: transparent;
  }
  .link:hover:after {
    border-color: #000006;
    content: "";
    right: 0;
  }
}

* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

*:focus {
  outline-color: #d0021b;
  outline-offset: 0;
  outline-width: medium;
}

* * {
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
}

::-moz-selection {
  background: #4c4f50;
  color: #ffffff;
}

::selection {
  background: #4c4f50;
  color: #ffffff;
}

.component {
  opacity: 1;
  -webkit-transition: opacity 0.4s ease-in;
  transition: opacity 0.4s ease-in;
}

.component.is-oov {
  opacity: 0;
}

.button {
  background-color: $base-color;
  border: 2px solid transparent;
  color: #ffffff;
  display: inline-block;
  font-size: 14px;
  font-weight: 600;
  min-width: 140px;
  padding: 7px;
  position: relative;
  text-decoration: none;
  text-transform: uppercase;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.button:focus, .button:hover {
  background-color: $base-color-hover;
}

.button.alt {
  background-color: transparent;
  background-image: -webkit-gradient(linear, left top, left bottom, from(transparent), color-stop(49%, transparent), color-stop(50%, #ffffff), to(#ffffff));
  background-image: linear-gradient(180deg, transparent 0%, transparent 49%, #ffffff 50%, #ffffff 100%);
  background-size: 200% 200%;
  color: #ffffff;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  border: 2px solid #ffffff;
}

.button.alt:focus {
  background-position: 0 100%;
  color: #000006;
}

@media screen and (hover: hover) {
  .button.alt:hover {
    background-position: 0 100%;
    color: #000006;
  }
}

input {
  border: 1px solid #ffffff;
  height: 40px;
  padding: 10px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

input[type=email], input[type=text] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

input.is-invalid {
  border-color: #000006;
  color: #a41e37;
}

.main-wrapper {
  margin: 0 auto;
  width: 100%;
}

@-webkit-keyframes side-to-side-r {
  0% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
  }
  50% {
    -webkit-transform: translate(4px, 0);
    transform: translate(4px, 0);
  }
  100% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
  }
}

@keyframes side-to-side-r {
  0% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
  }
  50% {
    -webkit-transform: translate(4px, 0);
    transform: translate(4px, 0);
  }
  100% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
  }
}

@-webkit-keyframes side-to-side-l {
  0% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
  }
  50% {
    -webkit-transform: translate(-4px, 0);
    transform: translate(-4px, 0);
  }
  100% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
  }
}

@keyframes side-to-side-l {
  0% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
  }
  50% {
    -webkit-transform: translate(-4px, 0);
    transform: translate(-4px, 0);
  }
  100% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
  }
}

.masthead-wrapper {
  letter-spacing: normal;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  -webkit-transform: translateY(0);
  transform: translateY(0);
  -webkit-transition: -webkit-transform 0.5s ease-out;
  transition: -webkit-transform 0.5s ease-out;
  transition: transform 0.5s ease-out;
  transition: transform 0.5s ease-out, -webkit-transform 0.5s ease-out;
  width: 100%;
  z-index: 999;
}

@media screen and (min-width: 900px) {
  .masthead-wrapper {
    position: relative;
  }
}

.masthead-wrapper.is-hidden {
  overflow: hidden;
  -webkit-transform: translateY(-100%);
  transform: translateY(-100%);
}

.masthead-wrapper *:focus {
  outline-color: #ffffff;
}

.nas .content-wrapper--masthead {
  margin-bottom: 0 !important;
}

.nas .content-wrapper--masthead .masthead {
  position: -webkit-sticky;
  position: sticky;
}

.icon-phone:before {
  content: "\e942";
}

.icon-envelop:before {
  content: "\e945";
}

.icon-whatsapp:before {
  content: "\ea93";
}

.icon-vk:before {
  content: "\ea98";
  font-size: 16px;
}

@media (min-width: 768px) {
  .col-20 {
    flex: 0 0 20%;
    max-width: 20%;
  }

  .col-40 {
    flex: 0 0 40%;
    max-width: 40%;
  }
}





